<script>
import img from '../assets/fondoazul.png';
import profile from '../assets/user.png';
import ProgressBar from '../components/ProgressBar';

export default {
    name: 'DeptoBase',
    props: {
        goal:{
            required: true,
        },
        progress: {
            required: true,
        },
        name: {
            required: true,
        },
        depto: {
            required: true,
        },
        days: {
            required: true,
        },
        m3: {
            required: true,
        },
        bonus_p: {
            required: true,
        }
        ,
        bonus_tot: {
            required: true,
        },
        asis: {
            required: true,
        },
        extra: {
            required: true,
        },
        bonus_prod: {
            required: false
        },
        bonus_goals: {
            required: false
        }
    },
    components: {ProgressBar},
    data() {
        return {
            background: img,
            user: profile
        }
    },
    created() {
        this.$store.dispatch("admin/week");
    },
    computed:{
        week() {
            return this.$store.getters["admin/getWeek"];
        }
    },
    filters:{
        split: (value) => {
            if (!value) return '';
            value = value.toString();

            let splitStr = value.toLowerCase().split(' ');
            let len = splitStr.length
            let i;
            
            for ( i = 0; i < len; i++) {
                splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);     
            }
            let nameCap = splitStr.join(' '); 
            let newName = nameCap.split(' ').slice(0, -1).join(' ')

            return newName;
        },
        rand: (value) => {
            if (!value || value < 0) return '0';
            if (typeof value === 'number') {
                return value.toFixed(2);
            }
            return value;
        },
        cap: (value)=>{
            if (!value) return '';
            value = value.toString();
            
            let splitStr = value.toLowerCase().split(' ');
            let len = splitStr.length
            let i; 
            for ( i = 0; i < len; i++) {
                splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);     
            }
            let nameCap = splitStr.join(' '); 
            return nameCap;
        }
    }
}
</script>